/* BASE STYLE */
* {
  outline: none;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  color: #333;
}

body {
  font-family: Roboto;
  background-color: #f5f9fb;
  color: #263240;
  display: flex;
  flex-direction: column;
}

.content {
  flex-grow: 1;
  flex-shrink: 1;
}

.navbar {
  flex-grow: 0;
  flex-shrink: 0;
}

.secondary_nav {
  flex-grow: 0;
  flex-shrink: 0;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
  color: #263240 !important;
  font-family: "Quicksand", Helvetica Neue, Arial, sans-serif;
}

h1 {
  font-family: Quicksand;
  text-transform: uppercase;
  margin: 30px 0px 30px 0px;
}

h1 .bold {
  font-weight: 700;
}

a {
  color: #2f9fda;
}

a:hover {
  color: #3cafeb;
}

li {
  list-style-type: square;
}

input[type="text"], input[type="email"], input[type="password"], .textarea, textarea[type="text"] {
  border-radius: 5px;
  background-color: white;
  border: 1px solid #000000;
  color: #333;
  box-shadow: none;
  outline: none;
}

input[type="text"]:focus, input[type="email"]:focus, input[type="password"]:focus, .textarea:focus, textarea[type="text"]:focus {
  border: 1px solid #2f9fda;
  box-shadow: none;
}

.textarea, textarea {
  resize: vertical;
}

.btn {
  background-color: #E2C029;
  color: white;
  box-shadow: none;
  transition: ease 0.3s background-color;
}

.btn:hover, .btn:active, .btn:focus {
  background-color: #E2C029;
  color: white;
  transition: ease 0.3s background-color;
}

.container {
  margin: 30px auto;
}

/* Custom, iPhone Retina */
/* Large Devices, Wide Screens */
.navbar {
  background-color: #263240;
  border-radius: 0;
  border: none;
  margin-bottom: 0;
  z-index: 10;
}

.navbar .logo {
  background: url(/assets/images/logo_apptite.svg) no-repeat center;
  background-size: 100px;
  width: 130px;
  text-indent: -9999px;
  transition: ease 0.3s opacity;
}

.navbar .logo:hover {
  opacity: 0.7;
  transition: ease 0.3s opacity;
}

.navbar-nav > li > a {
  color: #f5f9fb !important;
}

.navbar-default .navbar-right li a {
  background-color: transparent;
  transition: 0.3s ease all;
}

.navbar-default .navbar-right li a:hover {
  background-color: #2f9fda;
  transition: 0.3s ease background-color;
}

.navbar-default .navbar-right .open > a, .navbar-default .navbar-right .open > a:focus, .navbar-default .navbar-right .open > a:hover, .navbar-default .navbar-right .open > a:active {
  border: 2px solid #2f9fda;
}

.navbar-default .navbar-collapse {
  border-color: #263240;
}

.navbar-default .navbar-toggle {
  border: none;
}

.navbar-default .navbar-toggle:hover, .navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:active {
  background-color: #2f9fda;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #f5f9fb;
}

.navbar-right li a.nav-login {
  background-color: #2f9fda;
  color: white !important;
}

.navbar-right li a.nav-login:hover {
  background-color: #014D6F;
}

@media (min-width: 768px) {
  .navbar-right li a.nav-login {
    display: inline;
    border-radius: 4px;
    padding: 10px 15px;
  }
  .navbar-right li a.nav-register {
    display: inline;
    padding: 8px 13px;
    border-radius: 4px;
    margin-right: 10px;
    border: 2px solid #2f9fda;
    box-sizing: border-box;
  }
  .navbar-brand,
  .navbar-nav > li > a {
    line-height: 66px;
    height: 66px;
    padding-top: 0;
  }
  .nav-btn, .nav-btn-account {
    width: 40px;
    height: 40px !important;
    border-radius: 50%;
    border: 2px solid #2f9fda;
    text-indent: -9999px;
    margin: 13px 0 0 20px;
  }
  .upload {
    background: url(../images/icon_upload.svg) no-repeat center;
    background-size: 40%;
  }
  .nav-btn-account {
    text-indent: 0;
    border: none;
    overflow: hidden;
  }
  .nav-btn-account img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    /* Half the height */
    margin-left: -20px;
    /* Half the width */
  }
  .dropdown-menu {
    border-radius: 6px !important;
    border: none;
    margin-top: 15px !important;
    background-color: #263240;
    padding: 0 0;
    overflow: hidden;
  }
  .dropdown-menu li {
    border-top: 1px solid #223652;
  }
  .dropdown-menu li a {
    color: #f5f9fb;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .dropdown-menu li a:hover {
    background: transparent !important;
    color: #2f9fda;
  }
}

@media (max-width: 768px) {
  .navbar .logo {
    left: 50%;
    right: 50%;
    text-align: center;
    position: absolute;
    margin-left: -65px;
  }
  .profile_pic {
    display: none;
  }
}

.hero {
  background-image: url(/assets/images/hero_banner.jpg);
  background-size: cover;
  position: relative;
  height: 100vh;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
}

.header {
  position: absolute;
  top: 40%;
  color: #f5f9fb;
  text-align: center;
  width: 100%;
  color: #fff;
  font-size: 100px;
  -ms-transform: translate(0, -50%);
  /* IE 9 */
  -webkit-transform: translate(0, -50%);
  /* Safari */
  transform: translate(0, -50%);
}

.header .header-logo {
  display: block;
  width: 400px;
  margin: 0 auto 15px auto;
}

.header h2 {
  color: #f5f9fb !important;
  font-weight: 200;
  font-size: 20px;
}

.footer-distributed {
  background-color: #263240;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: "Roboto" 16px sans-serif;
  padding: 55px 50px;
  margin-top: 80px;
}

.footer-distributed .footer-left, .footer-distributed .footer-center, .footer-distributed .footer-right {
  display: inline-block;
  vertical-align: top;
}

.footer-distributed .footer-left {
  width: 40%;
}

.footer-distributed .footer-logo {
  background: url(/assets/images/logo_apptite.svg) no-repeat center;
  background-size: 100px;
  width: 130px;
  height: 50px;
  text-indent: -9999px;
  transition: ease 0.3s opacity;
}

.footer-distributed .footer-logo:hover {
  opacity: 0.7;
  transition: ease 0.3s opacity;
}

.footer-distributed .footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0;
}

.footer-distributed .footer-links a {
  display: inline-block;
  line-height: 1.8;
  text-decoration: none;
  color: inherit;
}

.footer-distributed .footer-links a:hover {
  opacity: 0.5;
}

.footer-distributed .footer-company-name {
  color: #8f9296;
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-center {
  width: 35%;
}

.footer-distributed .footer-center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin: 10px 15px;
  vertical-align: middle;
}

.footer-distributed .footer-center i.fa-envelope {
  font-size: 17px;
  line-height: 38px;
}

.footer-distributed .footer-center i:hover {
  background-color: #2f9fda;
}

.footer-distributed .footer-center p {
  display: inline-block;
  color: #ffffff;
  vertical-align: middle;
  margin: 0;
}

.footer-distributed .footer-center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2;
}

.footer-distributed .footer-center p a {
  color: #2f9fda;
  text-decoration: none;
}

.footer-distributed .footer-right {
  width: 20%;
}

.footer-distributed .footer-company-about {
  line-height: 20px;
  color: #92999f;
  font-size: 13px;
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-company-about span {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px;
}

.footer-distributed .footer-icons {
  margin-top: 25px;
}

.footer-distributed .footer-icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  margin-right: 3px;
  margin-bottom: 5px;
}

.footer-distributed .footer-icons a:hover {
  background-color: #2f9fda;
}

